<template>
  <div id="box">
    <div id="border" class="my-5 py-5">
    <!-- 1.  -->
    <div id="title" class="mb-5">
      <span><img alt="Vue logo" src="@/assets/udonge.png" style="width: 10%"></span>
      <span class="font-weight-bold " style="color: #695549;"> </span>
    </div>
    <!-- 2.  -->
    <b-form @submit.prevent="onSubmit">
      <b-row id="accountBox" class="ml-5">
        <b-col cols="10">
          <ValidationProvider
            name=""
            rules="required|min:5"
            v-slot="{ errors }"
          >
            <b-form-group>
              <label
                style="float:left; padding-right:10px; padding-top:5px"
                for="userId"
                >:
              </label>
              <b-form-input
                type="text"
                style="width:50%; float:left;"
                v-model="credentials.userId"
                placeholder=" "
                required
                autofocus
              ></b-form-input>
              <br>
              <br>
              <small class="text-danger" style="margin-top:5px">{{
                errors[0]
              }}</small>
            </b-form-group>
          </ValidationProvider>
        </b-col>
      </b-row>
      <b-row id="accountBox" class="ml-5">
        <b-col cols="10">
          <ValidationProvider
            name=""
            rules="required|email"
            v-slot="{ errors }"
          >
            <b-form-group>
              <label
                style="float:left; padding-right:10px; padding-top:5px"
                for="email"
                >:
              </label>
              <b-form-input
                type="email"
                style="width:50%; float:left;"
                v-model="credentials.email"
                placeholder=" "
                required
                @keypress.enter="onSubmit()"
              ></b-form-input>
              <b-button type="submit" size="sm" style="background-color: #695549;"></b-button>
              <br>
              <br>
              <small class="text-danger" style="float:left; margin-top:5px">{{
                errors[0]
              }}</small>
            </b-form-group>
          </ValidationProvider>
        </b-col>
      </b-row>
    </b-form>
    <b-form v-if="this.possible_post" @submit.prevent="onSubmit2">
      <b-row id="accountBox" class="ml-5">
        <b-col cols="10">
          <ValidationProvider
            name=""
            rules="required"
            v-slot="{ errors }"
          >
            <b-form-group>
              <label
                style="float:left; padding-right:10px; padding-top:5px"
                for=""
                >:
              </label>
              <b-form-input
                type="text"
                style="width:50%; float:left;"
                v-model="emailcode"
                placeholder=""
                required
              ></b-form-input>
              <b-button style="background-color: #695549;" size="sm" type="submit" class="btn btn-secondary"></b-button>
              <small class="text-danger" style="float:left; margin-top:5px">{{
                errors[0]
              }}</small>
            </b-form-group>
          </ValidationProvider>
        </b-col>
      </b-row>
    </b-form>
    <b-row v-if="this.possible_post && !this.possible_id">
       <small class="text-danger" style="float:left; margin-top:5px"
        >   </small>
    </b-row>


    <div v-if="this.makepassword">
      <b-form @submit.prevent="onSubmit3">
        <div class="container" style="padding:10px">
          <ValidationProvider
            name=""
            rules="required|min:6"
            v-slot="{ errors }"
          >
            <b-form-group>
              <label
                style="float:left; padding-right:10px; padding-top:5px"
                for="password"
                >:
              </label>
              <b-form-input
                type="password"
                style="width:50%; float:left;"
                v-model="credentials2.password"
                placeholder=" "
                required
              ></b-form-input>
              <small class="text-danger" style="float:left; margin-top:5px">{{
                errors[0]
              }}</small>
            </b-form-group>
          </ValidationProvider>
        </div>

        <div class="container" style="padding:10px">
          <ValidationProvider
            name=" "
            rules="required|confirmed:"
            v-slot="{ errors }"
          >
            <b-form-group>
              <label
                style="float:left; padding-right:10px; padding-top:5px"
                for="password_confirmation"
                > :
              </label>
              <b-form-input
                type="password"
                style="width:50%; float:left;"
                v-model="credentials2.password_confirmation"
                placeholder=" "
                required
              ></b-form-input>
              <small class="text-danger" style="float:left; margin-top:5px">{{
                errors[0]
              }}</small>
            </b-form-group>
          </ValidationProvider>
        </div>
        <b-button style="background-color: #695549;" type="submit" class="btn btn-secondary"
          >  </b-button
        >
      </b-form>
    </div>
    </div>
    <br>
    <br>
    <b-button id="btn_signup" class="mx-3" @click="$router.push({ name: 'Login' })"></b-button>
  </div>
</template>

<script>
import axios from "axios";
import { ValidationProvider } from "vee-validate";
const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  components: {
    ValidationProvider,
    // extend,
  },
  name: "FindPassword",
  data() {
    return {
      credentials: {
        userId: "",
        email: "",
      },
      credentials2: {
        password: "",
        password_confirmation: "",
      },
      possible_post: false,
      possible_id: false,
      makepassword: false,
      emailcode: "",
    };
  },
  methods: {
    onSubmit() {
      axios
        .get(
          `${SERVER_URL}/user/password?userId=${this.credentials.userId}&email=${this.credentials.email}`
        )
        .then(() => {
          alert("   .");
          this.possible_post = true;
          this.possible_id = true;
        })
        .catch(() => {
          alert(" .");
          this.credentials.userId = "";
          this.credentials.email = "";
        });
    },
    // onSubmit2     post
    async onSubmit2() {
      axios
        .post(`${SERVER_URL}/user/email/${this.emailcode}`)
        .then(() => {
          alert("   ");
          this.makepassword = true;
        })
        .catch(() => {
          alert(" .");
          this.emailcode = "";
        });
    },
    async onSubmit3() {
      if (this.credentials2.password != this.credentials2.password_confirmation) {
        alert("  .");
        this.credentials2.password = "";
        this.credentials2.password_confirmation = "";
      } else {
        axios
          .put(`${SERVER_URL}/user/password`, { password : this.credentials2.password, userId : this.credentials.userId})
          .then(() => {
            alert("   ");
            window.location.href = "/account";
            
          })
          .catch(() => {
            alert("   .");
            this.password = "";
            this.password_confirmation = "";
          });
      }
    },
  },
};
</script>

<style>
input[type="password"] {
  font: small-caption;
  font-size: 16px;
}
::placeholder {
  font-family: "Jeju Gothic", sans-serif;
}
</style>
