<template>
  <b-form id="box" @submit="onSubmit">
    <!-- 1.   -->
    <b-row class="pb-5"  align-h="between">
      <!--   -->
      <b-col class="ml-3">
        <Profile />
      </b-col>

      <!-- /  -->
      <b-col align-self="center">
        <!-- <b-row class="mb-2">
          <div>/</div>
        </b-row> -->
        <b-row class="mb-3" align-h="center">
          <toggle-button
            :value="club.isOpen"
            :width="80"
            :height="35"
            :labels="{checked: '', unchecked: ''}"
            :color="{
              checked: '#695549', unchecked: '#a0a0a0'
            }"
            disabled
          />
        </b-row>
        <!-- bootstrap > Form File   -->
        <b-row align-h="center">
          <b-form-file
            v-model="club.fileId"
            :state="Boolean(file1)"
            placeholder=" "
            drop-placeholder="Drop file here..."
            style="width: 70%;"
          ></b-form-file>
          <!-- <div class="mt-3">Selected file: {{ file1 ? file1.name : '' }}</div> -->
        </b-row>
      </b-col>
    </b-row>
    <!--   -->
    <b-row class="ml-3 mb-5">
      <h3 style="text-align: left;"><span class="font-weight-bold">{{ dong }}</span>  </h3>
    </b-row>

    <!--  -->
    
    <div class="mb-5" style="text-align: left;">
      <b-col>
        <b-row class="mt-2">
          <b-col sm="3">
            <h4 class="font-weight-bold"></h4>
          </b-col>
          <b-col sm="7">
            <b-form-input
            class="font-weight-bold"
            :placeholder="club.clubName"
            disabled
          ></b-form-input>
          </b-col>
        </b-row>
      </b-col>
    </div>
    <!--  -->
    <div class="mb-5 pb-5" style="text-align: left;">
      <b-col>
        <h4 class="font-weight-bold mb-4"></h4>
          <b-form-textarea
            v-model="club.clubContent"
            placeholder=" !"
            rows="8"
          ></b-form-textarea>
      </b-col>
    </div>
    <!--   -->
    <div id="group_profile_buttons">
      <b-button class="mx-3" variant="danger" @click="deleteGroup"></b-button>
      <b-button class="mx-3" variant="success" @click="toMemberList"></b-button>
      <b-button class="mx-3" variant="info" @click="updateGroup"></b-button>
    </div>
    <br>
    <br>
  </b-form>
</template>

<script>
import Profile from '@/components/app/Profile'
import axios from 'axios'

export default {
  name: 'GroupUpdate',
  components: {
    Profile
  },
  // props 
  data: function () {
    return {
      user: '',
      dong: '',
      club: {
        clubName: "",
        clubContent: "",
        isOpen: true,
        imgURL: "",
      }
    }
  },
  methods: {
    deleteGroup: function () {
      const config = this.getToken()
      axios.delete("/club", this.club, config)
      .then(() => {
        this.$router.push({name: 'GroupPage', query: {club: this.club}})
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getToken: function () {
      const token = localStorage.getItem('jwt')
      const config = {
        headers: {
          Authorization: `JWT ${token}`
        }
      }
      return config
    },
    onSubmit(evt) {
      evt.preventDefault()
      alert(JSON.stringify(this.article))
    },
    updateGroup: function () {
      const config = this.getToken()
      axios.put("/club", this.club, config)
      .then(() => {
        this.$router.push({name: 'GroupPage', query: {club: this.club}})
      })
      .catch((err) => {
        console.log(err)
      })
    },
    toMemberList: function () {
      this.$router.push({name: 'GroupMemberList', query: {club: this.club}})
    },
  },
  created: async function() {
      //   !!
      // await this.getGroupInfo
  }
}
</script>

