<template>
  <b-form id="box">
    <!-- 1.   -->
    <b-row class="pb-5"  align-h="between">
      <!--   -->
      <b-col class="ml-3">
        <!-- props:  :fileId="Club.fileId" -->
        <Profile />
      </b-col>

      <!-- /  -->
      <b-col align-self="center">
        <!-- <b-row class="mb-2">
          <div>/</div>
        </b-row> -->
        <b-row class="mb-3" align-h="center">
          <toggle-button
            :value="club.isOpen"
            :width="80"
            :height="35"
            :labels="{checked: '', unchecked: ''}"
            :color="{
              checked: '#695549', unchecked: '#a0a0a0'
            }"
            style="font-size: small; text-align: center;"
            disabled
          />
        </b-row>
      </b-col>
    </b-row>
    <!--   -->
    <b-row class="ml-3 mb-5">
      <h3 style="text-align: left;"><span class="large">{{ dong }}</span> </h3>
    </b-row>

    <!--  -->
    
    <div id="group_profile_name" class="mb-5" style="text-align: left;">
      <b-col>
        <b-row class="mt-2">
          <b-col sm="3">
            <h4 class="font-weight-bold"></h4>
          </b-col>
          <b-col sm="7">
            <b-form-input
            class="font-weight-bold"
            :placeholder="club.clubName"
            disabled            
          ></b-form-input>
          </b-col>
        </b-row>
      </b-col>
    </div>
    <!--  -->
    <div class="mb-5 pb-5" style="text-align: left;">
      <b-col>
        <h4 class="font-weight-bold mb-4"></h4>
          <b-form-textarea
            :placeholder="club.clubContent"
            rows="8"
            disabled            
          ></b-form-textarea>
      </b-col>
    </div>
    <!--   -->
    <div id="group_profile_buttons">
      <!-- <b-button class="mx-3" variant="danger"></b-button> -->
      <!-- 1. v-if   -->
      <div v-if="isJoin">
        <b-button class="mx-3" variant="danger" @click="leaveGroup"></b-button>
        <b-button class="mx-3" variant="info" @click="toMemberList"></b-button>
      </div>
      <!-- 2. v-else (   ) -->
      <b-button v-else class="mx-3" type="submit" variant="info" @click="joinGroup"></b-button>
    </div>
    <br>
    <br>
  </b-form>
</template>

<script>
import Profile from '@/components/app/Profile'
import axios from 'axios'

export default {
  name: 'GroupProfile',
  components: {
    Profile,
  },
    // clubId props .
  props: {
  },
  data: function () {
    return {
      user: '',
      dong: '',
      club: {
        clubName: "",
        clubContent: "",
        isOpen: true,
        fileId: "",
      },
      isJoined: false,
    }
  },
  computed: {
    isJoin: function () {
      return this.isJoined
    }
  },
  methods: {
    toMemberList: function () {
      // props  clubId  .
      // this.$router.push({name: 'GroupMemberList', clubId })
    },
    joinGroup: function () {
      const config = this.getToken()
      //  club   ??
      axios.post("/club/member", this.user, config)
      .then(() => {
        this.$router.push({name: 'GroupPage', query: {club: this.club}})
      })
      .catch((err) => {
        console.log(err)
      })
    },
    leaveGroup: function () {
      const config = this.getToken()
      axios.delete("/club/member", this.user, config)
      .then(() => {
        this.$router.push({name: 'NewsFeed'})
      })
      .catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

