<template>
  <!-- !! -->
  <!-- -  &     -->

  <!--   ! -->
  <!-- 1. ()    ,    ! -->
  <div id="box">
    <div id="border" class="my-5 py-5">
      <!-- 1.  -->
      <div id="title" class="mb-5">
        <span><img alt="Vue logo" src="@/assets/udonge.png" style="width: 10%"></span>
        <span class="font-weight-bold " style="color: #695549;"></span>
      </div>
      <!-- 2.  -->
      <!-- 2.1 input -->
      <b-col class="mx-3">
          <!-- 2.1.1  -->
        <b-row id="accountBox">
          <b-col id="label" cols="3"><span></span></b-col>
          <b-col id="label_content" cols="5">
            <b-form-input
              type="text"
              :placeholder="user.userId"
              style="text-align: center;"
              disabled
            ></b-form-input>
          </b-col>
        </b-row>
        <!-- 2.1.2  -->
        <b-row id="accountBox">
          <b-col id="label" cols="3"><span></span></b-col>
          <b-col id="label_content" cols="5">
            <b-form-input
              v-model="user.nickname"
              type="text"
              :placeholder="user.nickname"
              style="text-align: center;"
              @keypress="switchCheck"
            ></b-form-input>
            <small v-if="!check" class="text-danger">  .</small>
            <div v-if="check === true" class="mt-2">
              <small class="text-success">  .</small>
              <b-button class='ml-2' style="background-color: #695549;"  size="sm" @click="updateNickname"></b-button>
            </div>
          </b-col>
          <b-col cols="2">
            <b-button
              style="margin-top:3px; background-color: #695549;"
              size="sm"
              @click="checkNickname"
            ></b-button>
            <br>
          </b-col>
        </b-row>
        <!-- 2.1.3  -->
        <b-row id="accountBox">
          <b-col id="label" cols="3"><span></span></b-col>
          <b-col id="label_content" cols="5">
            <b-form-input
              type="email"
              :placeholder="user.email"
              style="text-align: center;"
              disabled
            ></b-form-input>
          </b-col>
        </b-row>
        <!-- 2.1.4   -->
        <b-row id="accountBox">
          <b-col id="label" cols="3"><span></span></b-col>
          <b-col id="label_content" cols="5" class="font-weight-bold">
            {{ dong }}
          </b-col>
          <b-col cols="2">
            <b-button style="background-color: #695549;" size="sm" @click="addLocation"></b-button>
            <b-button variant="danger" size="sm" @click="deleteLocation"></b-button>
          </b-col>
        </b-row>
        <!-- 2.1.5   -->
        <b-row id="accountBox">
          <b-col id="label" cols="3"><span></span></b-col>
          <b-col id="label_content" cols="5">
            <b-form-input
              type="password"
              v-model="password"
              placeholder="  ."
              style="text-align: center;"
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row id="accountBox">
          <b-col id="label" cols="3"><span> </span></b-col>
          <b-col id="label_content" cols="5">
            <b-form-input
                type="password"
                v-model="passwordConf"
                placeholder="  ."
                style="text-align: center;"
            ></b-form-input>
            <div v-if="isCorrect !== -1">
              <small class="text-danger">   .</small>
              <!-- <small v-else class="text-success">  !</small> -->
            </div>
          </b-col>
          <b-col cols="2">
            <b-button
              style="margin-top:3px; background-color: #695549;"
              size="sm"
              v-b-modal="'passwordModal'"
            ></b-button>
            <b-modal id="passwordModal" @ok="updatePassword"><img alt="Vue logo" src="@/assets/udonge.png" style="width: 10%"> ?</b-modal>
          </b-col>
        </b-row>
      </b-col>
    </div>
    <div>
      <!-- modal -->
      <b-button class="mx-3" variant="danger" v-b-modal="'deleteModal'"></b-button>
      <b-modal id="deleteModal" @ok="deleteAccount"><img alt="Vue logo" src="@/assets/udonge.png" style="width: 10%"> ...?</b-modal>
      <b-button class="mx-3"  style="background-color: #695549;" @click="toDetail"></b-button>
      </div>
  </div>
</template>

<script>
import axios from 'axios'

const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: 'AccountUpdate',
  data: function () {
    return {
      dong: '',
      user: {
        userId: "",
        nickname: "",
        email: "",
      },
      //   ?
      isCheck: -1,

      //  
      password: '',
      passwordConf: '',
      //  
      isCorrect: -1,
    }
  },
  computed: {
    //     .
    check: function () {
      return this.isCheck
    },
    //    
    correct: function () {
      if (this.password === this.passwordConf) {
        return true
      } else {
        return false
      }
    },
  },
  methods: {
    addLocation: function () {
      alert(" 2   !!?? \n  !")
    },
    checkNickname: function() {
      if(this.user.nickname.length < 2){
        alert("2    ~!")
        this.user.nickname = "";
        this.isCheck = false
      }
      else{
        axios
          .get(`${SERVER_URL}/user/nickname/${this.user.nickname}`)
          .then(() => {
            alert("   .");
            this.isCheck = true;
          })
          .catch(() => {
            if (this.user.nickname != "") {
              alert("   .");
              this.user.nickname = "";
              this.isCheck = false
            }
          });
      }
    },
    deleteAccount: function () {
      axios
          .delete(`${SERVER_URL}/user`, this.user)
          .then(() => {
            alert("  . \n     .")
            this.$router.push({ name: 'Login' })
          })
          .catch((err) => {
            alert("      \n  .")
            console.log(err)
          })
    },
    deleteLocation: function () {
      alert('     !')
    },
    toDetail: function () {
      this.$router.push({ name: 'AccountDetail' })
    },
    switchCheck: function () {
      this.isCheck = -1
    },
    updateNickname: function () {
      console.log(this.user)
      axios.put(`${SERVER_URL}/user`, this.user)
        .then(() => {
          console.log('  !!!!')
          alert("  !");
          this.isCheck = -1

          //  
          const userInfo1 = JSON.parse(localStorage.getItem('Login-token'))
          const userInfo2 = JSON.parse(localStorage.getItem('Info-token'))
          userInfo1.nickname = this.user.nickname
          userInfo2.nickname = this.user.nickname

          //  !!!!!
          localStorage.setItem("Login-token", JSON.stringify(userInfo1));
          localStorage.setItem("Info-token", JSON.stringify(userInfo2));
          // this.$router.push({ name: 'AccountDetail' })
          // location.replace('/accuont/detail')
          

        })
        .catch((err) => {
          console.log(err)
          alert("  .")
        })
    },
    updatePassword: function () {
      if (this.password !== this.passwordConf) {
        this.isCorrect = false
      }
      else {
        const data = {
          userId: this.user.userId,
          password: this.password
        }
        axios.put(`${SERVER_URL}/user/password`, data)
          .then(() => {
            this.isCorrect = -1
            alert(" !")
          })
          .catch((err) => {
            alert(" !")
            console.log(err)
          })
      }
      this.password = ''
      this.passwordConf = ''
    },
  },
  mounted() {
    const userInfo = JSON.parse(localStorage.getItem('Info-token'))
    this.user.userId = userInfo["userId"]
    this.user.nickname = userInfo["nickname"]
    this.user.email = userInfo["email"]
  }
}
</script>

<style>
#accountBox {
  margin-left: 30px;
  /* margin-right: 30px; */
  margin-bottom: 30px;
}

#border {
  border: 3px dotted #695549;
  /* opacity: 0.8; */
}

#label {
  padding-right: 0px;
  margin-right: 0px;
}

#label_content {
  padding-left: 0px;
  margin-left: 0px;
}
</style>