<template>
  <div id="box"  style="padding:10px;">
    <div id="border" class="my-5 py-5">
      <!-- 1.  -->
      <div id="title" class="mb-5">
        <span><img alt="Vue logo" src="@/assets/udonge.png" style="width: 10%"></span>
        <span class="font-weight-bold " style="color: #695549;"></span>
      </div>
      <!-- 2.  -->
      <!-- 2.1 input -->
      <b-col class="mx-3">
          <!-- 2.1.1  -->
        <b-row id="accountBox">
          <b-col align-self="left">
            <ValidationProvider
              name=""
              rules="required|min:5"
              v-slot="{ errors }"
            >
              <b-form-group>
                <label
                  style="float:left; padding-right:10px; padding-top:5px"
                  for="userId"
                  >:
                </label>
                <b-form-input
                  type="text"
                  class="ml-5"
                  style="width:50%; float:left;"
                  v-model="credentials.userId"
                  placeholder=" "
                  required
                  autofocus
                  @keypress.enter="check_user_id"
                ></b-form-input>
                <b-button id="btn_signup"
                  style="margin-top:3px"
                  size="sm"
                  @click="check_user_id"
                  ></b-button
                >
                <br>
                <br>
                <small id = "error1" class="text-danger" style="float:center; margin-top:5px">{{
                  errors[0]
                }}</small>
              </b-form-group>
            </ValidationProvider>
          </b-col>
        </b-row>
        <!-- 2.1.2  -->
        <b-row id="accountBox">
          <b-col align-self="left">
              <ValidationProvider
                name=""
                rules="required|min:2"
                v-slot="{ errors }"
              >
                <b-form-group>
                  <label
                    style="float:left; padding-right:10px; padding-top:5px"
                    for="nickname"
                    >:
                  </label>
                  <b-form-input
                    type="text"
                    class="ml-5"
                    style="width:50%; float:left;"
                    v-model="credentials.nickname"
                    placeholder="  "
                    required
                    @keypress.enter="check_user_nickname"
                  ></b-form-input>
                  <b-button id="btn_signup"
                    style="margin-top:3px"
                    size="sm"
                    @click="check_user_nickname"
                    ></b-button
                  >
                  <br>
                  <br>
                  <small id = "error2" class="text-danger" style=" margin-top:5px">{{
                    errors[0]
                  }}</small>
                </b-form-group>
              </ValidationProvider>
          </b-col>
        </b-row>
        <!-- 2.1.3  -->
        <b-row id="accountBox">
          <b-col align-self="left">
            <ValidationProvider
              name=""
              rules="required|email"
              v-slot="{ errors }"
            >
              <b-form-group>
                <label
                  style="float:left; padding-right:10px; padding-top:5px"
                  for="email"
                  >:
                </label>
                <b-form-input
                  type="email"
                  class="ml-5"
                  style="width:50%; float:left;"
                  v-model="credentials.email"
                  placeholder=" "
                  required
                  @keypress.enter="check_user_email"
                ></b-form-input>
                <b-button id="btn_signup"
                  style="margin-top:3px"
                  size="sm"
                  @click="check_user_email"
                  > </b-button
                >
                <br>
                <br>
                <small id = "error3"  class="text-danger" style=" margin-top:5px">{{
                  errors[0]
                }}</small>
              </b-form-group>
            </ValidationProvider>
          </b-col>
        </b-row>
        <!-- 2.1.4   -->
        <b-row v-if="this.possible_email && this.credentials.email" id="accountBox">
          <b-col align-self="left">
            <ValidationProvider
              name=""
              rules="required|min:4"
              v-slot="{ errors }"
            >
              <b-form-group>
                <label
                  style="float:left; padding-right:10px; padding-top:5px"
                  for="emailCode"
                  >:
                </label>
                <b-form-input
                  type="text"
                  class="ml-4"
                  style="width:50%; float:left;"
                  v-model="emailCode"
                  placeholder=" "
                  required
                  @keypress.enter="check_user_emailCode"
                ></b-form-input>
                <b-button id="btn_signup"
                  style="margin-top:3px"
                  size="sm"
                  @click="check_user_emailCode"
                  > </b-button
                >
                <br>
                <br>
                <small id = "error4" class="text-danger" style=" margin-top:5px">{{
                  errors[0]
                }}</small>
              </b-form-group>
            </ValidationProvider>
          </b-col>
        </b-row>
        <!-- 2.1.5   -->
        <b-row id="accountBox">
          <b-col align-self="left">
            <ValidationProvider
              name=""
              rules="required|min:6"
              v-slot="{ errors }"
            >
              <b-form-group>
                <label
                  style="float:left; padding-right:10px; padding-top:5px"
                  for="password"
                  >:
                </label>
                <b-form-input
                  type="password"
                  class="mx-4"
                  style="width:50%; float:left; font-color : black;"
                  v-model="credentials.password"
                  placeholder=" "
                  required
                   @keypress.enter="onSubmit()"
                ></b-form-input>
                <br>
                <br>
                <small  id = "error5"  class="text-danger" style=" margin-top:5px">{{
                  errors[0]
                }}</small>
              </b-form-group>
            </ValidationProvider>
          </b-col>
        </b-row>
        <b-row id="accountBox">
          <b-col align-self="left">
            <ValidationProvider
              name=" "
              rules="required|confirmed:"
              v-slot="{ errors }"
            >
              <b-form-group>
                <label
                  style="float:left; padding-right:10px; padding-top:5px"
                  for="password_confirmation"
                  >:
                </label>
                <b-form-input
                  type="password"
                  style="width:50%; float:left;"
                  v-model="password_confirmation"
                  placeholder="    "
                  required
                  @keypress.enter="onSubmit()"
                ></b-form-input>
                <br>
                <br>
                <small id = "error6"  class="text-danger" style=" margin-top:5px">{{
                  errors[0]
                }}</small>
              </b-form-group>
            </ValidationProvider>
          </b-col>
        </b-row>
      </b-col>
    </div>
    <!--  -->
    <div>
      <b-button id="btn_signup" class="mx-3" @click="$router.push({ name: 'Login' })">
         
      </b-button>
      <b-button id="btn_signup" class="mx-3" @click="onSubmit()">
        
      </b-button>
    </div>
    

      <!-- modal -->
      <!-- <b-button  variant="danger" v-b-modal="'deleteModal'"></b-button>
      <b-modal id="deleteModal" @ok="deleteAccount"><img alt="Vue logo" src="@/assets/udonge.png" style="width: 10%"> ?</b-modal>
      <b-button  style="background-color: #695549;" @click="toDetail"></b-button> -->
  </div>
</template>

<script>
import axios from "axios";
import { ValidationProvider } from "vee-validate";

const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  components: {
    ValidationProvider,
    // extend,
  },
  name: "Singup",
  data() {
    return {
      credentials: {
        userId: "",
        nickname: "",
        email: "",
        password: "",
        
      },
      password_confirmation: "",
      possible_email : false,
      emailCode : "",
      
      checkId : false,
      checkNickname : false,
      checkEmail : false,
      checkEmailCode : false,
    };
  },
  methods: {
    onSubmit() {
        if( 
          this.checkId == false || this.checkNickname == false || this.checkEmail == false || this.checkEmailCode == false ||
          document.getElementById("error5").innerHTML != "" || document.getElementById("error6").innerHTML != "" 
          )
           {
             alert("     .")
        }else{
          axios.post(`${SERVER_URL}/user`,this.credentials)
          .then(()=>{
           alert(" ");
           window.location.href = "/account";
           
          })
          .catch(()=>{
            alert("  .   .");
             window.location.href = "/account";
          });
        }
      
      
    },
    check_user_id: function() { 
      if(this.credentials.userId == "" || document.getElementById("error1").innerHTML != ""){
        this.credentials.usrId = "";
        alert("   .")
      }
      else{
        axios
          .get(`${SERVER_URL}/user/id/${this.credentials.userId}`)
          .then(() => {
            alert("   .");
            this.checkId = true;
          })
          .catch(() => {
            if (this.credentials.userId != "") {
              alert("   .");
              this.credentials.userId = "";
            }
          });
      }
    },
    check_user_nickname: function() {
       if(this.credentials.nickname == ""|| document.getElementById("error2").innerHTML != ""){
        alert("  .")
        this.credentials.nickname = "";
      }
      else{
        axios
          .get(`${SERVER_URL}/user/nickname/${this.credentials.nickname}`)
          .then(() => {
            alert("   .");
            this.checkNickname = true;
          })
          .catch(() => {
            if (this.credentials.nickname != "") {
              alert("   .");
              this.credentials.nickname = "";
            }
          });
      }
    },
     check_user_email: function() {
      if(this.credentials.email == "" || document.getElementById("error3").innerHTML != ""){
        alert("   .")
        this.credentials.email = "";
      }
      else{
        axios
          .post(`${SERVER_URL}/user/email`,this.credentials)
          .then(() => {
            alert("   .  .");
            this.checkEmail = true;
            this.possible_email = true;
          })
          .catch(() => {
            if (this.credentials.email != "") {
              alert("   .");
              this.credentials.email = "";
            }
          });
      }
    },
    check_user_emailCode: function() {
      if(this.emailCode == "" || document.getElementById("error4").innerHTML != ""){
        alert("  .")
      }
      else{
        axios
          .post(`${SERVER_URL}/user/email/${this.emailCode}`)
          .then(() => {
            this.checkEmailCode = true;
            alert("");
          })
          .catch(() => {
            if (this.emailCode != "") { 
              alert("  .");
              this.emailCode = "";
            }
          });
      }
    },
 
  },
};
</script>
<style>
#btn_signup {
  background-color: #695549;
}

input[type="password"] {
  font: small-caption;
  font-size: 16px;
}
::placeholder {
  font-family: "Jeju Gothic", sans-serif;
}
</style>
